      <!-- 分组管理、企业管理按钮 -->
    <!-- <ManageBtn />
    <GroupManage />
    

    <TemplateList />
    <SelectTemType />
    <MsgTemplate />
    <EditMsgTemplate />


    <VideoTemplate />
    <CompanyList   />
    <SearchCompany
      class="searchCom"
